.container-form {
    display: flex;
    margin: 2rem 0;
    align-items: center;
    justify-content: center;
  }
  
  .input {
    background-color: #eaeaf0;
    border-radius: 8px;
    padding: 0.8rem;
  }
  
  .weatherSearch {
    margin: 0 15px;
    border: none;
    color: #7c7c94;
  }
  
  .weatherIcon {
    color: #cfcfcf;
  }
  
  
  .container-mid {
    margin: 3rem 0;
  }